.header {
    background-color: #0E243C;
    padding: 15px 15px 0 15px;
}


@media (max-width : 768px) {
    .hide_on_moblie {
        display: none;
    }
    .header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 10;
        padding: 0;
    }
}